export default function pathMaker(code: string): string {
  const paths = window.location.pathname.split("/").filter((e) => e.length > 0);
  const host = window.location.host;
  if (paths.length <= 1) {
    paths.push(code);
  } else {
    paths[paths.length - 1] = code;
  }

  return `${host}/${paths.join("/")}`;
}
